<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  created() {
    // 获取localstorage中的值,解析为
    // 因为localstorage中的值是以键值对的形式存在的
    const counterMap = JSON.parse(localStorage.getItem("goods")) || {};
    this.$store.commit("setCounterMap", counterMap);
  },
};
</script>

